.payment_logo_container{
    width: 70px;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--brand-color);
    border-radius: 100%;
    padding: 5px;
    word-wrap: break-word;
    word-break: break-all;
    cursor: pointer;
}
.payment_logo_container >img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}


@media screen and (max-width: 768px){
    .payment_logo_container{
        width: 40px;
        height: 40px;
    }
}